import React from 'react';
import * as THREE from 'three';
import * as OBJLoader from 'three-obj-loader';
import { OrbitControls } from 'three-orbitcontrols';
import obj from '../../resources/models/room3.obj';
OBJLoader(THREE);

class ThreeContainer extends React.Component{
  constructor(props){
    super(props);
    this.loaded = false;
    this.loadObj = this.loadObj.bind(this);
    this.start = this.start.bind(this);
    this.stop = this.stop.bind(this);
    this.animate = this.animate.bind(this);
    this.renderScene = this.renderScene.bind(this);
  }
componentWillReceiveProps(nextProps) {
  if(nextProps.details && !this.loaded) {
    let images = nextProps.details.images;
    console.log(images);
    for(let i=0;i<images.length;i++) {
      if(images[i])
        this.loadPicture(images[i].src, images[i].matrix, i);
    }
    this.maxIndex = images.length;
    this.loaded = true;
  }
}
componentDidMount(){
    document.addEventListener('mousemove', this.onMouseMove, false);
    document.addEventListener('mousedown', this.onMouseDown, false);
    document.addEventListener('mouseup', this.onMouseUp, false);

    this.THREE = THREE;
    const width = this.mount.clientWidth;
    const height = this.mount.clientHeight;
    //ADD SCENE
    let scene = new THREE.Scene();
    this.scene = scene;
    //ADD CubeMap
    this.scene.background = new THREE.CubeTextureLoader()
        .setPath( 'https://res.cloudinary.com/dvn7y9omc/image/upload/v1551799368/cubemap/' )
        .load( [
          'px.png',
          'nx.png',
          'py.png',
          'ny.png',
          'pz.png',
          'nz.png'
        ] );
    //ADD CAMERA
    this.camera = new THREE.PerspectiveCamera(
      75,
      width / height,
      0.1,
      3000
    );
    this.camera.position.set(100, 100, 100);
    this.lastPos = this.camera.position;
    //ADD Raycaster
    this.raycaster = new THREE.Raycaster();
    this.mouse = new THREE.Vector2();
    //ADD RENDERER
    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.renderer.setClearColor('rgb(0,0,0)');
    this.renderer.setSize(width, height);
    this.mount.appendChild(this.renderer.domElement);
    //ADD CONTROLS
    this.controls = new this.THREE.OrbitControls( this.camera, this.renderer.domElement );
    this.controls.target.set(100.01,100,100.01);
    this.lastTarget = this.controls.target;
    this.controls.enableZoom = false;
    this.controls.panSpeed = 3000.0;
    this.controls.rotateSpeed = 0.08;
    this.controls.enableDamping = true;
    this.controls.dampingFactor = 0.1;
    // instantiate a loader
    this.loader = new this.THREE.OBJLoader();
    //ADD OBJ
    this.loader.load(
      obj,
      // called when resource is loaded
      this.loadObj,
      // called when loading is in progresses
      function (xhr) {
        console.log((xhr.loaded / xhr.total * 100) + '% loaded' );
      },
      // called when loading has errors
      function (error) {
        console.log( 'An error happened' + error);
      }
    );

    

    //ADD Light
    let light = new THREE.PointLight( 'rgb(255,255,255)', 1, 2000 );
    light.position.set( 60, 60, 60 );
    this.scene.add( light );
    let light2 = new THREE.AmbientLight( 'rgb(255,255,255)', 0.2, 2000 );
    light.position.set( 60,60,60 );
    this.scene.add( light2 );
    this.start();
}
loadPicture(src, matrix, index) {
  //ADD Frame
  let geometry = new THREE.BoxGeometry( 100, 100, 6 );

  let textureLoader = new THREE.TextureLoader();
  let map = textureLoader.load(src);
  let material = new THREE.MeshPhongMaterial({map: map});
  
  let mesh = new THREE.Mesh( geometry, material );
  
  mesh.position.fromArray(matrix.position);
  mesh.rotation.fromArray(matrix.rotation);
  mesh.name = index;

  this.scene.add( mesh );
}
addPicture(material, intersects, src, id) {
  //ADD Frame
  let geometry = new THREE.BoxGeometry( 100, 100, 5 );
  let mesh = new THREE.Mesh( geometry, material );
  mesh.position.set(intersects[0].point.x + intersects[0].face.normal.x*3, 
        intersects[0].point.y + intersects[0].face.normal.y*3, 
        intersects[0].point.z + intersects[0].face.normal.z*3);
  
  mesh.rotation.x = (intersects[0].face.normal.y) * Math.PI / 2 * Math.cos(intersects[0].face.normal.z);
  mesh.rotation.y = -(intersects[0].face.normal.x) * Math.PI / 2 * Math.cos(intersects[0].face.normal.z);
  mesh.name = this.maxIndex+1;
  this.maxIndex++;

  this.scene.add( mesh );

  this.props.saveExpo(mesh.name, src, id, mesh.getWorldPosition(), mesh.rotation);
}
componentWillUnmount(){
    this.stop();
    this.mount.removeChild(this.renderer.domElement);
}
start() {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate);
    }
}
stop() {
    cancelAnimationFrame(this.frameId);
}
distanceVector( v1, v2 )
{
    let dx = v1.x - v2.x;
    let dy = v1.y - v2.y;
    let dz = v1.z - v2.z;

    return Math.sqrt( dx * dx + dy * dy + dz * dz );
}
checkIntersection(intersects) {
  if(intersects.length > 0) {
    if(this.distanceVector(intersects[0].point, this.camera.position) <= 30){
      this.hit = true;
    }
  }
}
animate() {
  this.controls.update();
  this.hit = false;
  
  //Check if there is object tp remove
  if(this.props.removing && this.props.edit){
    this.scene.remove(this.scene.getObjectByName(this.props.removing));
  }

   // calculate objects intersecting the picking ray
   if(this.scene.children[2] != undefined){
    let dir = false;
    this.raycaster.set(this.camera.position, new THREE.Vector3(0,0,-1));
    let intersects = this.raycaster.intersectObjects( this.scene.children[2].children );
    this.checkIntersection(intersects);

    this.raycaster.set(this.camera.position, new THREE.Vector3(0,0,1));
    intersects = this.raycaster.intersectObjects( this.scene.children[2].children );
    this.checkIntersection(intersects);

    if(this.hit) dir = true;

    this.raycaster.set(this.camera.position, new THREE.Vector3(1,0,0));
    intersects = this.raycaster.intersectObjects( this.scene.children[2].children );
    this.checkIntersection(intersects);

    this.raycaster.set(this.camera.position, new THREE.Vector3(-1,0,0));
    intersects = this.raycaster.intersectObjects( this.scene.children[2].children );
    this.checkIntersection(intersects);

    if(this.hit){
      if(dir){
        this.camera.position.z = this.lastPos.z;
        this.controls.target.z = this.lastTarget.z;
      }
      else{
        this.camera.position.x = this.lastPos.x;
        this.controls.target.x = this.lastTarget.x;
      }
    } else {
      this.controls.panSpeed = 3000.0;
      this.lastPos = new THREE.Vector3(this.camera.position.x, this.camera.position.y, this.camera.position.z);
      this.lastTarget = new THREE.Vector3(this.controls.target.x, this.controls.target.y, this.controls.target.z);
    }
   } 
   

   this.mouse.lastX = this.mouse.x;
   this.mouse.lastY = this.mouse.y;
   this.renderScene();
   this.frameId = window.requestAnimationFrame(this.animate);
}
renderScene() {
  this.renderer.render(this.scene, this.camera);
}
loadObj(object) {
  const material = new THREE.MeshLambertMaterial({ color: 'rgb(255,255,255)'});
  object.traverse( function ( child ) {

    if ( child instanceof THREE.Mesh ) {

        child.material = material;

    }

  } );
  this.scene.add(object);
}

onMouseMove = (e) => {
  this.mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1;
  this.mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1;

  if(this.click && !this.moving && this.props.edit){
    //Raycast from camera
    this.raycaster.setFromCamera( this.mouse, this.camera );
    let intersects = this.raycaster.intersectObjects( this.scene.children, true );

    if(intersects.length > 0) {

      //Check if image is clicked
      if(intersects[0].object.parent.id !== this.scene.children[2].id) {
        this.selected = intersects[0].object;

        //Send coordinates for UI
        this.props.chosenImage(intersects[0].object.name, e);
        
        this.moving = true;
        this.controls.enabled = false;
      }
    }
  }

  if(this.moving && this.scene.children[2] && this.props.edit) {
    this.raycaster.setFromCamera( this.mouse, this.camera );
    let intersects = this.raycaster.intersectObjects( this.scene.children[2].children );

    if(intersects.length > 0) {
      //Set position based on the normal
      this.selected.position.set(intersects[0].point.x + intersects[0].face.normal.x*3, 
        intersects[0].point.y + intersects[0].face.normal.y*3, 
        intersects[0].point.z + intersects[0].face.normal.z*3);

      this.selected.rotation.x = (intersects[0].face.normal.y) * Math.PI / 2 * Math.cos(intersects[0].face.normal.z);
      this.selected.rotation.y = -(intersects[0].face.normal.x) * Math.PI / 2 * Math.cos(intersects[0].face.normal.z);

      //Send coordinates for UI
      this.props.chosenImage('', e);
    }
  }



  this.click = false;
}
onMouseDown = (e) => {
  if(!this.moving && this.props.edit) 
    setTimeout(() => {
      this.props.hideUI();
    }, 100);
  this.click = true;
}
onMouseUp = (e) => {
  if(this.moving && this.props.edit){
    this.props.saveExpo(this.selected.name, null, null, this.selected.getWorldPosition(), this.selected.rotation);
  }

  this.click = false;
  this.moving = false;
  this.controls.enabled = true;
}

dropped = (e) => {
  e.preventDefault();

  //Set mouse position
  this.mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1;
  this.mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1;
  
  //Start raycasting
  this.raycaster.setFromCamera( this.mouse, this.camera );
  
  if(this.scene.children[2] != undefined){
    let intersects = this.raycaster.intersectObjects( this.scene.children[2].children );
    
    //Add new picture
    if(intersects.length > 0) {
      let textureLoader = new THREE.TextureLoader();
      let map = textureLoader.load(e.dataTransfer.getData('text/plain'));
      let material = new THREE.MeshPhongMaterial({map: map});

      this.addPicture(material, intersects, e.dataTransfer.getData('text/plain'), e.dataTransfer.getData('text/id'));
      
    }
  }
}

render(){
    return(
      <div
        style={{ width: '100%', height: '100vh' }}
        ref={(mount) => { this.mount = mount }}
        onDragOver={(e) => {
          e.preventDefault();
          return false;
        }}
        onDrop={this.dropped}
      />
    )
  }
}
export default ThreeContainer