import React from 'react'
import '../../styles/dashboard.css'
import {Redirect, Link, withRouter} from 'react-router-dom'

const ExpoItem2 = ({src, title, expo3D, isAdmin, isAuthor, addTop, deleteExpo}) => (
    <li className='expo-item2'>
        <img src={src} className='expo-img2' onClick={expo3D}></img>
        <h2 className='title'>{title}</h2>
        {isAdmin ? <button className='expandbtn inline' onClick={addTop}><span>  -</span></button>:''}
        <button className='expandbtn inline' onClick={expo3D}><span></span></button>
        {(isAdmin || isAuthor) ? <button className='expandbtn inline' onClick={deleteExpo}><span></span></button>:''}
    </li>
);

export default ExpoItem2;